<template>
	<div id="activity-bargainList">
		<public-header title="天天特价"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group">
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">待生效</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">进行中</div>
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">已结束</div>
				</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
				<van-list v-if="!empty">
					<div class="list-item" v-for="(item, index) in activity" :key="item.id">
						<div class="padding-15 van-hairline--bottom">
							<div class="flex margin-15-b">
								<span class="font-15 padding-10-r">{{item.title}}</span>
								<span class="c-gray" v-if="item.status == 1">
									剩余<span class="c-danger font-14">{{item.until}}</span>天
								</span>
							</div>
							<p class="desc">活动类型: {{item.type_cn}}</p>
							<p class="desc">活动时间: {{item.starttime_cn}}~{{item.endtime_cn}}</p>
							<p class="desc">创建日期: {{item.addtime_cn}}</p>
						</div>
						<div class="btn-group">
							<span class="btn-item bg-danger" @click="onChangeDelete(item)">撤销</span>
						</div>
						<div class="status">
							<img class="wh-100" src="static/img/activity_not_start.png" alt="" v-if="item.status == 2">
							<img class="wh-100" src="static/img/activity_ing.png" alt="" v-if="item.status == 1">
							<img class="wh-100" src="static/img/activity_end.png" alt="" v-if="item.status == 0">
						</div>
					</div>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>没有任何活动哦!</p>
				</div>
			</van-pull-refresh>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				activity: {},
				empty: false,
				filter: {
					items:{
						status: 1
					}
				}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				that.empty = false;
				getData({
					vue: that,
					url: 'manage/activity/bargain/bargainList',
					data: {
						filter: this.filter.items
					},
					success: function(res) {
						if(res.activity.length == 0) {
							that.empty = true;
						}
						that.activity = Object.assign({}, res.activity);
						that.isRefresh =  false;
					}
				})
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
				this.onLoad();
			},
			onPullDownRefresh(){
				this.onLoad();
			},
			onChangeDelete(item){
				var that = this;
				that.util.jspost({
					url: 'manage/activity/bargain/del',
					data: {
						id: item.id,
					},
					confirm: '确定撤销活动吗？',
				});
			}
		},
		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.filter.items.status = this.query.status;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#activity-bargainList .list-item{
		margin: 15px;
		border-radius: 5px;
		overflow: hidden;
		background-color: #fff;
		font-size: 14px;
		position: relative;
	}
	#activity-bargainList .list-item .desc{
		color: #7a7a7a;
		font-size: 13px;
		margin-top: 7px;
	}
	#activity-bargainList .list-item .btn-group {
		padding: 10px 15px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
	}
	#activity-bargainList .list-item .btn-group .btn-item{
		border: 1px solid #ccc;
		padding: 5px 15px;
		border-radius: 5px;
		margin-left: 10px;
	}
	#activity-bargainList .list-item .status{
		position: absolute;
		top: 0;
		right: 0;
		width: 50px;
		height: 50px;
	}
</style>


